.composition {
  position: relative;
  .composition__photo{
    width: 55%;
    box-shadow: 0 1.5rem 4rem rgba($color-black, .4);
    border-radius: 2px;
    position: absolute;
    transition: all .3s;
    z-index: 10;
    outline-offset: 2rem;
    &--p1 {
      left: 0;
      top: -2rem;
    }
    &--p2 {
      right: 0;
      top: 2rem;
    }
    &--p3 {
      left: 20%;
      top: 10rem;
    }
    &:hover {
      outline: 1.5rem solid $color-primary;
      transform: scale(1.05);
      box-shadow: 0 2.5rem 4rem rgba($color-black, .4);
      z-index: 20;
    }
  }
  
  &:hover &__photo:not(:hover) {
    transform: scale(.95);
  }
 
  // .composition:hover .composition__photo:not(:hover){
  //   transform: scale(.8);
  // }
  
}