.section-about {
  background-color: $colo-grey-light-1;
  padding: 25rem 0;
  margin-top: -20vh;
}

.section-about__wrapper {
  display: flex;
}

.section-about__block{
  width: 48%;
  margin: 0 2%;  
}

.section-features {
  padding: 25rem 0;
  display: flex;
  justify-content: space-between;
  background-image: linear-gradient(to right bottom ,#7ed56fc5, #28b485be), url(../../img/nat-4.jpg);
  background-size: cover;
  margin-top: -12rem;
  transform: skewY(-7deg);

  & > .container {
    transform: skewY(7deg);
  }
}

.section-tours {
  background-color: $colo-grey-light-1;
  padding: 32rem 0 15rem;
  margin-top: -20rem;
}

.wrapper {
  flex-direction: row;
  justify-content: space-between;
}

.section-stories {
  padding: 10rem 0 15rem;
  position: relative;
}

.section-book {
  padding: 15rem;
  background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);
}

.book {
  background-image: linear-gradient(105deg,
  rgba(#fff, .9) 0%,
  rgba(#fff, .9) 50%,
  transparent 50%
  ) ,url(/img/nat-10.jpg);
  background-size: 100%;
  border-radius: 3px;
  box-shadow: 0 1.5rem 4rem rgba(#000000, $alpha: .25);
  height: 48rem;

  .book__form {
    width: 50%;
    padding: 6rem;
  }
}