.header {
  position: relative;
  background-image: linear-gradient(to right bottom ,#7ed56fc5, #28b485be), url(../img/hero.jpg);
  height: 95vh;
  background-position: top;
  background-size: cover;

  clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);

  &__logo-box {
    position: absolute;
    top: 40px;
    left: 40px;
  }
  
  &__logo {
    height: 35px;
  }
  
  &__text-box {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate( -50%, -50% );
    text-align: center;
  }
}

.heading-primary {
  color: #fff;
  text-transform: uppercase;

  backface-visibility: hidden;
  margin-bottom: 60px;

  &--main {
    display: block;
    font-size: 60px;
    font-weight: 400;
    letter-spacing: 35px;
  
    animation-name: moveInLeft;
    animation-duration: 1s;
    animation-timing-function: ease-out;
  }
  
  &--sub {
    display: block;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 16.2px;
  
    animation-name: moveInRight;
    animation-duration: 1s;
    animation-timing-function: ease-out;
  }
}